import { Component, OnInit } from '@angular/core';
import{ Router } from '@angular/router';
import{ChatBotService} from './chat-bot.service';


@Component({
  // selector: 'app-chat-bot', removing thsi sleector because we are using routing
  templateUrl: './chat-bot.component.html',
  styleUrls: ['./chat-bot.component.css']

   
})
export class ChatBotComponent implements OnInit {
    errorMessage: string;
 input: any ={
   inputMessage:"",
   sessionID:"2",
   class:"left",
   user:"Me"
 };
  Messages : any = [];
output:object;
  constructor(private chatbotService: ChatBotService) { 
}
getMessage ():void{
  console.log(this.input);

  this.Messages.push(Object.assign({}, this.input));
  
   this.chatbotService.getmessage(this.input)
                .subscribe(output => this.doSomething(output),
                error => this.errorMessage = <any>error);
                           
}
 doSomething(data):void{
console.log(data);
if(data.result){
let requestData = {
  inputMessage:data.result.fulfillment.speech,
   sessionID:"2",
   class:"right",
   user:"AI"
}
this.Messages.push(requestData);
let ele = document.getElementsByClassName('messages');
    let eleArray = <Element[]>Array.prototype.slice.call(ele);
    eleArray.map( val => {
      setTimeout(()=>{    //<<<---    using ()=> syntax
      val.scrollTop = val.scrollHeight;
      this.input.inputMessage = "";
 },200);
        
    });
console.log("message :" +this.Messages);
}

            }          
  ngOnInit() {
  }

}



